<template>
  <div>
    <div class="box">
      <div class="box_top">
        <van-search
          placeholder="请输入手机号"
          autofocus
          maxlength="11"
          v-model="keyword"
          shape="round"
          class="search_box"
        />
        <van-button
          round
          color="#00887a"
          size="small"
          class="search_btn"
          :disabled="loading"
          @click="searchbtn"
        >搜索</van-button>
      </div>
      <div class="box_mid">
          <div slot="content">
            <van-cell-group v-if="list.length > 0">
              <van-cell
                v-for="(item, index) in list"
                :key="index"
                @click="chat(item)"
              >
              <template slot="title">
                <img :src="imgbox[item.headimg].src" class="imgbg" />
                  <div class="imgcot">
                    <div>{{item.name}}</div>
                    <div>({{item.tel}})</div>
                  </div>
              </template>
              </van-cell>
            </van-cell-group>
          </div>
      </div>
      <load :loading="loading"></load>
    </div>
  </div>
</template>

<script>
import load from "@/components/loading/loading";
import { mapState } from 'vuex'
export default {
  name: "sousuo",
  components: {
    load
  },
  data() {
    return {
      keyword: "",
      loading: false,
      list: [],
      imgbox: [
        { id: 0, src: require("../../../assets/fourth/manf.png") },
        { id: 1, src: require("../../../assets/fourth/womanf.png") },
        { id: 2, src: require("../../../assets/fourth/mans.png") },
        { id: 3, src: require("../../../assets/fourth/womans.png") }
      ]
    };
  },
  computed: {
    ...mapState(["userInfo"])
  },
  methods: {
    searchbtn() {
      this.list= []
      this.loading = true;
      var arr = JSON.parse(JSON.stringify(this.userInfo.friends))
      if(arr.length > 0) {
        setTimeout(() => {
          this.loading = false;
        }, 500);
        for(var i = 0; i < arr.length; i++) {
          if(JSON.stringify(arr[i].tel).indexOf(this.keyword) != -1) {
            this.list.push(arr[i])
          }
        }
      } else {
        setTimeout(() => {
          this.loading = false;
        }, 500);
        this.list = []
      }
    },
    chat(item) {
      this.$router.push({
        path: '/chatinfo',
        query: {
          id: item
        }
      })
    }
  }
};
</script>
<style lang="scss" scoped>
.content_app {
  height: calc(100vh - 46px) !important;
}
.scroll-list-wrap {
  height: calc(100vh - 100px) !important;
}
.box_top {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  .search_box {
    flex: 11;
  }
  .search_btn {
    flex: 1;
  }
}
.imgbg {
  width:40px;
  height:40px;
  float: left;
}
.imgcot {
  float: left;
  margin-top: -4px;
  margin-left: 5px;
}
.box_mid {
  position: relative;
  height: calc(100vh - 100px);
}
.box {
  position: relative;
  height: calc(100vh - 46px);
  .loading {
    position: absolute;
    left: 50%;
    bottom: 50%;
    z-index: 2;
  }
}
</style>